body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pointer-text {
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
}

.dont-select-text {
  -webkit-user-select: none;
  user-select: none;
}

.link{
  margin-left: 16px;
  color: #000;
  text-decoration: none;
}

.MUIDataTableSelectCell-root-720{
  display: block;
}

.blog{
  padding: 5px;
  border: solid 1px darkgrey;
   width: 90%;
   max-width: 400px;
   word-wrap: break-word;
    overflow: hidden;
 }

.blog-image{
  width: calc(100% - 10px);
  margin: 5px;
}

.blog-title{
  margin: 5px;
  display: -webkit-flex;
  display: flex;
  text-align: left;
  width: 100%;
  margin-bottom: 10px

}

.blog-text{
  margin: 5px;
  display: -webkit-flex;
  display: flex;
  text-align: left;
  width: 100%;

}

